        title: 'Erkin Koray - Meçhul (1968)',
        image: './posters/erkin-koray-mechul.jpg',
        info: (<div className='content-capsule'><p>Erkin Koray, 1941’de istanbul’da doğdu. 1965 yılında Almanya'nın Hamburg şehrine giden Erkin Koray 1966 yılında Türkiye'ye dönüşünden sonra Erkin Koray Dörtlüsü adlı grubu kurdu. 1967 yılında basılan, bir yüzünde "Kızları da Alın Askere", diğer yüzünde "Aşk Oyunu" adlı parçalar bulunan 45'liği ile önemli başarı kazandı. Özellikle "Kızları da Alın Askere" parçası Erkin Koray'ın geniş kitleler tarafından tanınmasında önemli rol oynamıştır. 1968 yılında, Hürriyet Gazetesi tarafından yapılan "Altın Mikrofon" yarışmasına katıldı. Bu yarışmada 4. olan Erkin Koray'ın yarışmadaki şarkıları "Meçhul" ve "Çiçek Dağı" daha sonra bir plak şirketi tarafından piyasaya çıkarılarak 800 bin adet gibi büyük tiraj yaptı. Bu ilk önemli başarıyı, 60'lı yılların sonlarına dek ardı ardına gelen: "Anma Arkadaş", "Hop Hop Gelsin", "Sana Bir Şeyler Olmuş", "Seni Her Gördüğümde" gibi hepsi büyük beğeni toplayan parçalar takip etmiştir.</p><p>1969 yılında kurduğu Yeraltı Dörtlüsü adlı grubu ile Türkiye'de ilk "Underground" müzik akımının öncüsü oldu. 70'li yılların başlarına gelindiğinde Koray Türkiye'de oldukça geniş bir dinleyici kitlesine sahiptir ve kendine özgü müzik çizgisi belirginleşmiş durumdadır.</p><p>1971'de Erkin Koray Süper Grup, 1972'de Ter gruplarını kuran Koray , 1970-1974 yılları arasında Türkiye müzik listelerinde üst sıralarda yer alan klasikleşmiş birçok esere imza atmıştır. "İlahi Morluk", "Aşka İnanmıyorum", "Mesafeler", "Züleyha", "Silinmeyen Hatıralar", 1974'te "Şaşkın", "Fesuphanallah" bu dönem eserlerindendir.</p><p>Yenilikçi, sentezci, deneyci bir müzik çizgisi olan Erkin Koray; sıra dışı şarkı sözleri, kendine özgü vokal biçemi, uzun saçları, özgün kıyafetleri ve bunun gibi daha birçok nedenle dönemin yayın tekeli olan Türkiye Radyo Televizyon Kurumu TRT tarafından dışlanmıştır. Eserlerinin nerede ise tamamı yakın zamanlara kadar, TRT denetimi tarafından yayımlanmaya uygun görülmemiştir. Bu durum Türkiye'de özel yayın kuruluşlarının ortaya çıkışına kadar süregelmiş ve Koray'ın dinleyici kitlesinin sınırlı kalmasına yol açmıştır.</p></div>),
