        info: (<div className='content-capsule'><p>One of the first representatives of Turkish rock music, Erkut Taçkın started his career during his pupilage with Naval Military College Band. Taçkın who went to Germany as a factoy worker in 1962, couldn’t stay away from music and joined the band named Black Point. The band’s name changed to Erkut Taçkın and Durul Gence Quintet. After making music in Germany for a while, the band turned back to Turkey and became a pioneer of beat music. Taçkın joined the band, Okan Dinçer and Kontrastlar in 1968 and released Mühür Gözlüm including the single named Özlem.</p></div>),
