        info: (<div className='content-capsule'><p>Kaygısızlar were formed in 1965 by Fuat Güner, Ender Arol and Semih Aksoy. Shortly after, Fuat Güner met Mazhar Alanson and asked him to join the band. They went through some lineup changes and finally, in 1967, they met with Barış Manço. They released 8 singles together, which were deeply influenced by beat music and psychedelic rock. Their first single without Barış Manço, “Şaşırdım / Son Gece” was released in 1969 by Sayan label. “Şaşırdım” was an interpretation of the song “Short Circuit” by the rock band The ID. After releasing 3 more singles, the band split in 1971.</p></div>),
