        info: (<div className='content-capsule'><p>Founded in 1967, Moğollar was one of the biggest bands in Turkey during the 70’s. They were among the founders of Anatolian rock with a melodic, psychedelic and blues-influenced sound, and they were also heralded as the kings of Anatolian pop. Their popularity even spread to Europe and earned them many prestigious awards. They worked with countless musicians, ranging from Barış Manço to Selda Bağcan. Even though they took a long hiatus between 1976 and 1992, they are still making music, which puts them among the most active Turkish bands.</p><p>Their first and also most famous and influential album, Anadolu Pop, was first released in 1971 in France. 2 years later, it was released in Turkey by Yavuz Plak. An instrumental piece, Haliç’te Güneşin Batışı (Sunset In Golden Horn) is the 7th song off the album. It clearly shows why these guys are called the fathers of Anatolian pop. The band’s unmistakable talent is on full display here; especially Murat Ses’ keyboard solos are very much ahead of the time.</p><p>An ethno driven - progressive song that took place in the “Danses Et Rythmes De La Turquie D'Hier À Aujourd'hui” album which won Académie Charles Cros award (had “Anadolu Pop” name in the Turkey release). Band first released the song on single Ternek / Haliç'te Güneşin Batışı in 1970 under Diskotür label.</p></div>),
